
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [ common, modal ],
components: {
Popup,
Flexbox,
FlexboxItem
},
props: {
classList: {
type: Array,
default: ()=>{
return []
}
},
value: {
type: [String,Number,Array],
default: ()=>{
return []
}
}
},
data () {
return {
toggleExpand: false,
model: this.value
}
},
computed: {
multiple(){
if(Array.isArray(this.value)){
return true
}
return false
},
selectedText () {
let model = this.model,
result = ''
if(this.multiple) {
if(model.length){
model.forEach(m=>{
result += this.classList.find(c=>c.cid==m).class_name + ','
})
result = result.substring(0,result.length-1)
}
}else{
result =  this.classList.find(c=>c.cid==model).class_name
}
return result
}
},
watch: {
'modal$.show': function (val) {
if(val){
this.model = util.copy(this.value)
}
}
},
methods: {
reset () {
this.model = []
},
ok () {
this.$emit('input',this.model)
this.close()
},
getItemIndex (row) {
let index = -1

if(this.multiple) {
index = this.model.indexOf(row.cid)
}else{
if(this.model==row.cid){
index = 1
}
}
return index
},
toggleCheck (row) {
if(this.multiple){
let index = this.model.indexOf(row.cid)
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push(row.cid)
}
}else{
this.model = row.cid
}
},
}
}



