	import util from '@/libs/util'
	import common from '@/libs/common.mixin'
	import StudentList from './list.vue'
	import PopupFilter from './popup-filter.vue'
	
	export default{
		mixins: [ common ],
		components: {
			PopupFilter,
			StudentList
		},
		data () {
			return {
				data: [],
				classList: [],
				search: {
					search_value: '',
					search_field: 'student_lesson_remain_hours',
					search_type: 'asc',
					cids: []
				}
			}
		},
		mounted () {
			this.init_data()
		},
		computed: {
			filterList () {
				let list = this.data,
					result = [],
					filter = (item)=>{
						return true
					},
					search = this.search
					
				if(!/^\s*$/.test(search.search_value)){
					filter = (item)=>{
						return item.student_name.indexOf(search.search_value)>-1||item.pinyin.indexOf(search.search_value)>-1||item.pinyin_abbr.indexOf(search.search_value)>-1
					}
				}
				
				result = list.filter(filter)
				
				if(search.cids.length>0){
					result = result.filter(r=>{
						let result = false
						r.cids.forEach(c=>{
							if(search.cids.includes(c)){
								result = true
							}
						})
						return result
					})
				}
				
				result = this.sortData(result,search.search_type,search.search_field)
								
				return result
			}
		},
		methods: {
			openFilter () {
				this.$refs.filter.show()
			},
			sortClick (field) {
				if(this.search.search_field == field){
					this.search.search_type = this.search.search_type=='asc'?'desc':'asc'  
				}else{
					this.search.search_field = field
					this.search.search_type = 'asc'
				}
				
			},
			isActive (field,type) {
				let result = '',
					search = this.search
								
				if(type){
					result = (search.search_field==field&&search.search_type==type)?'active':''
				}else{
					if(this.search.search_field==field){
						result = 'active'
					}
				}				
				
				return result
			},
			sortData (data, type, key) {                
                data.sort((a, b) => {                    
                    if (type === 'asc') {
                        return Number(a[key]) > Number(b[key]) ? 1 : -1;
                    } else if (type === 'desc') {
                        return Number(a[key]) < Number(b[key]) ? 1 : -1;
                    }                    
                });
                return data;
            },
			resetSearch () {
				this.search.search_value = ''	
				this.searchFocus()
			},
			searchFocus () {
				this.$refs.search.focus()
			},
			init_data () {
				let params = {
					class: 1
				}
				this.$rest('employees')
				.add_url_param(this.eid$)
				.add_url_param('students')
				.get(params)
				.success(data=>{
					this.dealStudents(data)
				}).error(response=>{
					this.toast(response.body.message||'获取学员失败','warn')	
				})
			},
			dealStudents (data) {
				let result = []
				if(data.list&&data.list.length>0){
					this.loopPushStudents(data.list)
				}
				if(data.class&&data.class.length>0){
					this.classList = data.class
					data.class.forEach(c=>{
						this.loopPushStudents(c.students,c.cid)
					})
				}						
			},
			loopPushStudents (list,cid) {
				cid = cid || 0
				if(list&&list.length>0){
					list.forEach(l=>{
						let index = this.data.findIndex(d=>d.sid==l.sid),
							obj = util.copy(l)
						if(index==-1){
							if(cid){
								this.$set(obj,'cids',[cid])
							}							
							this.data.push(obj)
						}else{
							if(cid){
								this.data.find(d=>d.sid==l.sid).cids.push(cid)								
							}
						}
					})
				}
			}
		}
	}



