
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default{
mixins: [ common ],
components: {
Card,
Flexbox,
FlexboxItem
},
props: {
list: {
type: Array,
default: ()=>{
return []
}
}
},
data () {
return {
map_sex: {0:'未确定',1:'男',2:'女'},
map_student_type: {0:'体验学员 ',1:'正式学员 ',2:'vip学员'},
map_status: {1:'正常',20:'停课',30:'休学',90:'退学',100:'封存'}
}
},
methods: {
serviceDetail (item) {
this.$router.push({path:'./service/'+item.sid})
},
getAge (item) {
return util.age(item.birth_time)
},
detail (item) {
this.$router.push({path:'./student/'+item.sid})
}
}
}



